import React, { Component } from 'react';
import Middle from '../component/Middle/Middle';
import RightSide from '../component/RightSide/RightSide';

export class ListTrack extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isPlay: '',
            choosenTrack: ''
        };
        this.playStop = this.playStop.bind(this);
    }

    componentWillReceiveProps(nextProps) {
        this.setState({
            choosenTrack: nextProps.inputVal,
            isPlay: ''
        })        
    }

    playStop(dataFromChild) {
        if (dataFromChild === true) {
            this.setState({
                isPlay: dataFromChild,
            }, () => console.log(this.state));
        }
        else if (dataFromChild === false) {
            this.setState({
                isPlay: dataFromChild,
            }, () => console.log(this.state));
        }
    }
    
    render() {
        const style = {
            height: '100%',
            width: '100%'
        };
        return (
            <div style={style} className='row'>
                <div className='col-md-5'>
                    <Middle id={this.props.match.params.id} choosenTrack={this.state.choosenTrack} update={this.props.update} />
                </div>
                <div className='col-md-7'>
                    <RightSide id={this.props.match.params.id} choosenTrack={this.state.choosenTrack} inputVal={this.state.isPlay} update={this.props.update} />
                </div>
            </div>
        )
    }
}

export default ListTrack
