import React, { Component } from 'react';
import './Bottom.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCheck, faRandom, faStepBackward, faStepForward, faPlayCircle, faRetweet, faListAlt, faDesktop, faVolumeUp } from '@fortawesome/free-solid-svg-icons';

export class Bottom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            artist: '',
            cover: '',
            tracks: [],
            track: '',
            album: [],
            playing: '',
        };
    }

    componentWillReceiveProps(nextProps) {
        //check if data is exist
        if (nextProps.inputVal.album !== "") {
            //set state when data has received
            this.setState({
                track: nextProps.inputVal.album.tracks[0],
                artist: nextProps.inputVal.album.tracks.artist,
                tracks: nextProps.inputVal.album.tracks.tracks,
                cover: nextProps.inputVal.album.album.cover,
                playing: nextProps.inputVal.album.playing,
                track: nextProps.inputVal.album.tracks.tracks[nextProps.inputVal.album.playing]
            });
        }
    }

    //Change playing track by click on Next or Previous button
    changeTrack(clickBtn) {
        //Next button
        if (clickBtn === 'next') {
            if (this.state.track.number < this.state.tracks.length) {
                this.setState({
                    track: this.state.tracks[this.state.track.number],
                    playing: this.state.playing + 1
                },
                    () => {
                        //Data for sending to parent component
                        const stateSend = {
                            album: this.props.inputVal.album.album,
                            playing: this.state.playing,
                            tracks: this.props.inputVal.album.tracks
                        }
                        //Send data through props
                        this.props.action(stateSend)
                    }
                );

            }
        }
        //Previous button
        else if (clickBtn === 'prev') {
            if (this.state.track.number - 1 > 0) {
                this.setState({
                    track: this.state.tracks[this.state.track.number - 2],
                    playing: this.state.playing - 1
                },
                    () => {
                        //Data for sending to parent component
                        const stateSend = {
                            album: this.props.inputVal.album.album,
                            playing: this.state.playing,
                            tracks: this.props.inputVal.album.tracks
                        }
                        //Send data through props
                        this.props.action(stateSend)
                    }
                );

            }
        }
    }


    render() {
        //style for setting cover on player bar
        const coverImg = {
            backgroundImage: `url(${this.state.cover})`
        };
        return (

            <div className='bottom row'>
                {/* Info side */}
                <div className='info col-md-3'>
                    <div style={coverImg} className='cover'>
                    </div>

                    <div>
                        <h5>{this.state.track.title}</h5>
                        <h6>{this.state.artist}</h6>
                    </div>
                    <FontAwesomeIcon className='icon checkIcon' icon={faCheck} color="#439e65" />
                </div>

                {/* Control side */}
                <div className='col-md-6 play-bar'>
                    <div className='control-button'>
                        <FontAwesomeIcon className='icon' icon={faRandom} />
                        <FontAwesomeIcon onClick={() => { this.changeTrack('prev') }} className='icon' icon={faStepBackward} />
                        <FontAwesomeIcon onClick={() => { console.log(this.props) }} className='icon play-btn' icon={faPlayCircle} />
                        <FontAwesomeIcon onClick={() => { this.changeTrack('next') }} className='icon' icon={faStepForward} />
                        <FontAwesomeIcon className='icon' icon={faRetweet} />

                    </div>
                    <div className='bar'>
                        <span>00:00</span>
                        <div className="progress">
                            <div className="progress-bar w-75" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
                        </div>
                        <span>{this.state.track.legnth}</span>
                    </div>

                </div>

                {/* Option side */}
                <div className='col-md-3 control-thing'>
                    <FontAwesomeIcon className='icon' icon={faListAlt} />
                    <FontAwesomeIcon className='icon' icon={faDesktop} />
                    <FontAwesomeIcon className='icon' icon={faVolumeUp} />
                    <div className="progress">
                        <div className="progress-bar w-50" role="progressbar" aria-valuenow="75" aria-valuemin="0" aria-valuemax="100"></div>
                    </div>
                </div>
            </div>
        )

    }
}

export default Bottom
