import React, { Component } from 'react';
import './RigthSide.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCopyright, faMusic } from '@fortawesome/free-solid-svg-icons';
import loading from '../../img/loading.gif'

export class RightSide extends Component {

    constructor(props) {
        super(props);
        this.state = {
            tracks: [],
            playing: 0,
            albumID: this.props.id,
            album: '',
            listTracks: '',
            isLoad: false
        };
    }


    componentWillReceiveProps(nextProps) {
        console.log(nextProps);
        //Check if data is exist
        if (nextProps.choosenTrack !== '') {
            //Get playing track
            this.setState({
                playing: nextProps.choosenTrack.album.playing
            });
        }
        //If Play button is hitten
        if (nextProps.inputVal === true) {
            this.setState({
                playing: 0,
            });
        }
        //If Stop button is hitten
        else if (nextProps.inputVal === false) {
            this.setState({
                playing: '',
            });
        }
    }

    //Convert track's duration to minute
    converTime(millis) {
        let minutes = Math.floor(millis / 60000);
        let seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    //Get track info and send to parent conponent when clicking on a track
    getTracks(index) {
        //Set active class for that track
        this.setState({
            playing: index
        }, () => {
            //Data for sending to parent component
            if (this.props.choosenTrack === "") {
                console.log(this.state);
                const stateSend = {
                    album: this.state.album,
                    playing: this.state.playing,
                    tracks: this.state.listTracks
                }
                //Send data through props
                this.props.update(stateSend);
            }
            else {
                const stateSend = {
                    album: this.props.choosenTrack.album.album,
                    playing: this.state.playing,
                    tracks: this.props.choosenTrack.album.tracks
                }
                //Send data through props
                this.props.update(stateSend);
            }

        });
    }
    render() {
        //Loading view will appear when data is getting
        if (this.state.isLoad === false) {
            return (
                <div className='loading'>
                    <div className='img-load'>
                        <img className='imgLoad' src={loading} alt='loading' />
                        <h2>Loading...</h2>
                    </div>
                </div>
            )
        }
        //View after data is gotten
        else {
            return (
                <div className='right-side'>
                    <div className='hide-bar'>
                        <ul>
                            {//Get list tracks
                                this.state.tracks.map((item, index) => {
                                    const className = this.state.playing === index ? 'active' : '';
                                    return (
                                        <li key={index}
                                            onClick={() => this.getTracks(index)}
                                            className={className}>
                                            <FontAwesomeIcon className='icon' icon={faMusic} />
                                            <div className='song-name'>{item.title}</div>
                                            <div className='duration'>{item.legnth}</div>
                                        </li>
                                    )
                                })
                            }
                        </ul>
                    </div>
                    <div className='copyright'>
                        <p><FontAwesomeIcon className='icon' icon={faCopyright} color="#ddd" /> 1999 ACT Music + Vision GmbH + Co KG</p>
                        <p><FontAwesomeIcon className='icon' icon={faCopyright} color="#ddd" /> 1999 ACT Music + Vision GmbH + Co KG</p>
                    </div>
                </div>
            )
        }
    }

    //Re-get data when reload at the same url
    componentDidMount = () => {
        //Get playing album for sending data
        console.log(this.props.choosenTrack);
        if (this.props.choosenTrack === "") {
            const url = `https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify-2/apis/yourLibrary.json`;
            fetch(url)
                .then(response => response.json())
                .then(data => {
                    this.setState({
                        album: data[this.props.id - 1]
                    });
                })
                .catch(e => console.log('error', e));
        }

        // Get list tracks by url ID
        const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${this.props.id}.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {
                //Convert track's duration to minute
                let dataChangeType = data.tracks;
                dataChangeType.map((item) => {
                    item.legnth = this.converTime(item.legnth);
                })
                //Set state
                this.setState({
                    tracks: data.tracks,
                    listTracks: data,
                    isLoad: true
                });
            })
            .catch(e => console.log('error', e));
    }
}

export default RightSide
