import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import SpotifyWebApi from 'spotify-web-api-js';
//Component
import LeftSideBar from './component/LeftSide/LeftSideBar';
import ListTrack from './component/ListTrack';
import Bottom from './component/Bottom/Bottom';
import Album from './component/Album/Album';
import Home from './component/Album/Home';
import Search from './component/Search/Search';
const spotifyApi = new SpotifyWebApi();
class App extends Component {

  constructor() {
    super()
    //get hashParam for access token
    const params = this.getHashParams();
    //get access token
    const token = params.access_token;
    spotifyApi.setAccessToken(token);
    console.log(token)
    this.state = {
      album: '',
      token: token
    };

    //callback getAlbum function for getting data from child component
    this.getAlbum = this.getAlbum.bind(this);
    console.log(this.state);
  }

  //getAlbum function for getting data from child component
  getAlbum(dataFromChild) {
    this.setState({
      album: dataFromChild,
    });
  }

  //get hashParam for access token
  getHashParams() {
    let hashParams = {};
    let e, r = /([^&;=]+)=?([^&;]*)/g,
      q = window.location.hash.substring(1);
    e = r.exec(q)
    while (e) {
      hashParams[e[1]] = decodeURIComponent(e[2]);
      e = r.exec(q);
    }
    return hashParams;
  }

  render() {
    return (
      <Router>
        <div className="App row">
          <div className='col-md-2'>
            <LeftSideBar getAlbum={this.state.album} action={this.getAlbum} />
          </div>
          <div className='col-md-10'>
            <Switch >
              <Route exact path="/" component={Home} />
              <Route path="/search" render={() => <Search accessToken={this.state.token} />} />
              <Route path="/album/:id" render={(props) => <ListTrack {...props} inputVal={this.state} update={this.getAlbum} />} />
              <Route path="/album">
                <Album action={this.getAlbum} />
              </Route>
            </Switch >

          </div>
          <div className='player-bar'>
            <Bottom inputVal={this.state} action={this.getAlbum} />
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
