import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMusic } from '@fortawesome/free-solid-svg-icons';
const TopResult = props => {
    return (
        <div className='row'>
            <div className="col-md-4">
                <div className="card">
                    {/* Get the first album through props */}
                    <img className="card-img-top" src={props.firstAlbum.images[0].url} alt="Cover" />
                    <div className="card-body">
                        <h6 className="card-title">{props.firstAlbum.name}</h6>
                        <p className="card-text">{props.firstAlbum.artists[0].name}</p>
                    </div>
                </div>
            </div>
            <div className="col-md-8">
                <div className='right-side'>
                    <div className='hide-bar'>
                        <ul>
                            {//Get list tracks through props then map it to every item
                                props.tracks.slice(0, 5).map((item) => {
                                return (
                                    <li key={item.id}>
                                        <FontAwesomeIcon className='icon' icon={faMusic} />
                                        <div className='song-name'>{item.name}</div>
                                        <div className='duration'>{item.duration_ms}</div>
                                    </li>
                                )
                            })
                            }
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default TopResult
