import React, { Component } from 'react';
import './Album.css';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import loading from '../../img/loading.gif'

export class Album extends Component {
    constructor(props) {
        super(props);
        this.state = {
            album: [],
            isLoad: false
        };
    }



    //Convert mili-sencond to minute
    converTime(millis) {
        let minutes = Math.floor(millis / 60000);
        let seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    // Get list tracks by album ID
    getListTrack(id, item) {
        const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${id}.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {
                //Convert duration of all tracks to minute before processing
                let dataChangeType = data.tracks;
                dataChangeType.map((item) => {
                    item.legnth = this.converTime(item.legnth);
                })
                //Data for sending to parent component
                const sendTracks = {
                    id: id,
                    tracks: data,
                    album: item,
                    playing: 0
                }
                //Send data through props
                this.props.action(sendTracks)
            })
            .catch(e => console.log('error', e));
    }

    render() {

        //Loading view appears while data is getting from API
        if (this.state.isLoad === false) {
            return (
                <div className='loading'>
                    <div className='img-load'>
                        <img className='imgLoad' src={loading} alt='loading' />
                        <h2>Loading...</h2>
                    </div>
                </div>
            )

        }

        //View after get data from API
        else {
            return (
                <div className='album'>
                    <h1>Albums</h1>
                    <div className='row'>
                        {//Get list albums by map
                            this.state.album.map((item, index) => {
                                return (
                                    <div key={index} className='col-md-2'>
                                        <Link to={"/album/" + (index + 1)} >
                                            <div onClick={() => this.getListTrack(index + 1, item)} className="card">
                                                <div className='album-wrap'>
                                                    <img className="card-img-top" src={item.cover} alt="Cover" />
                                                </div>

                                                <div className="card-body">
                                                    <h6 className="card-title">{item.album}</h6>
                                                    <p className="card-text">Huong Thanh</p>
                                                </div>
                                            </div>
                                        </Link>
                                    </div>
                                )
                            }
                            )
                        }
                    </div>
                </div>
            )
        }
    }

    // Get item from API
    componentDidMount = () => {
        const url = `https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify-2/apis/yourLibrary.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    album: data,
                    isLoad: true
                });
            })
            .catch(e => console.log('error', e));
    }
}


export default Album
