import React, { Component } from 'react';
import './LeftSide.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowAltCircleDown } from '@fortawesome/free-solid-svg-icons';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";


export class LeftSideBar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: '',
            avatar: '',
            recently: [],
            playing: '',
            choosing: 0,
            menu: '',
            album: ''
        };
        
        //Set active class for menu after reload match url 
        if(window.location.pathname.startsWith("/search")){
            this.state.menu = 1;
        }
        else if (window.location.pathname.startsWith("/album")){
            this.state.menu = 2;
        }
        else {
            this.state.menu = 0;
        }
    }
    componentWillReceiveProps(nextProps) {
        //When recent list already has items
        if (this.state.recently.length > 0) {
            //When recent list has less than 5 items and no duplicate item in the first position
            if (this.state.recently.length < 5 && this.state.recently[0].album !== nextProps.getAlbum.album.album) {
                //Add an item to the beginning
                this.state.recently.unshift({
                    album: nextProps.getAlbum.album.album,
                    type: 'album',
                    id: nextProps.getAlbum.id,
                    cover: nextProps.getAlbum.album.cover
                })
            }
            //When recent list has equal or more than 5 items and no duplicate item in the first position
            else if (this.state.recently.length >= 5 && this.state.recently[0].album !== nextProps.getAlbum.album.album) {
                //Remove the last item
                this.state.recently.splice(-1, 1);
                //Then add an item to the beginning
                this.state.recently.unshift({
                    album: nextProps.getAlbum.album.album,
                    type: 'album',
                    id: nextProps.getAlbum.id,
                    cover: nextProps.getAlbum.album.cover
                });
            }
        }
        //When recent list dosen't have any item
        else {
            this.state.recently.unshift({
                album: nextProps.getAlbum.album.album,
                type: 'album',
                id: nextProps.getAlbum.id,
                cover: nextProps.getAlbum.album.cover
            })
        }
    }

    //Convert mili-sencond to minute
    converTime(millis) {
        let minutes = Math.floor(millis / 60000);
        let seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    //Change active of recently list and send items to parent component for changing playing albums to recently album
    changeRecently(index, item) {
        const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${item.id}.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {

                //Convert duration of all tracks to minute before processing
                let dataChangeType = data.tracks;
                dataChangeType.map((item) => {
                    item.legnth = this.converTime(item.legnth);
                });

                //Data for sending to parent component
                const sendTracks = {
                    id: item.id,
                    tracks: data,
                    album: item,
                    playing: 0,
                    choosing: index,
                }
                //Send data through props
                this.props.action(sendTracks)
            })
            .catch(e => console.log('error', e));
    }

    //Change active of menu
    changeMenu(index) {
        this.setState({
            menu: index
        });
    }
    render() {
        return (
            <div className='left-container'>
                <img className='logo' alt='spotify' src='https://storage.googleapis.com/pr-newsroom-wp/1/2018/11/Spotify_Logo_RGB_White.png' />
                <br /><br />

                {/* Menu */}
                <ul className='menu'>
                    <Link to="/">
                        <li className={this.state.menu === 0 ? 'active-menu' : ''} onClick={() => this.changeMenu(0)}>
                            <svg viewBox="0 0 512 512" width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M448 463.746h-149.333v-149.333h-85.334v149.333h-149.333v-315.428l192-111.746 192 110.984v316.19z" fill="currentColor"></path></svg>
                            &nbsp; &nbsp; Home
                    </li>
                    </Link>
                    <Link to="/search">
                        <li className={this.state.menu === 1 ? 'active-menu' : ''} onClick={() => this.changeMenu(1)}>
                            <svg viewBox="0 0 512 512" width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M349.714 347.937l93.714 109.969-16.254 13.969-93.969-109.969q-48.508 36.825-109.207 36.825-36.826 0-70.476-14.349t-57.905-38.603-38.603-57.905-14.349-70.476 14.349-70.476 38.603-57.905 57.905-38.603 70.476-14.349 70.476 14.349 57.905 38.603 38.603 57.905 14.349 70.476q0 37.841-14.73 71.619t-40.889 58.921zM224 377.397q43.428 0 80.254-21.461t58.286-58.286 21.461-80.254-21.461-80.254-58.286-58.285-80.254-21.46-80.254 21.46-58.285 58.285-21.46 80.254 21.46 80.254 58.285 58.286 80.254 21.461z" fill="currentColor" fillRule="evenodd"></path></svg>
                            &nbsp; &nbsp; Search
                    </li>
                    </Link>
                    <Link to="/album">
                        <li className={this.state.menu === 2 ? 'active-menu' : ''} onClick={() => this.changeMenu(2)}>

                            <svg viewBox="0 0 512 512" width="24" height="24" xmlns="http://www.w3.org/2000/svg"><path d="M291.301 81.778l166.349 373.587-19.301 8.635-166.349-373.587zM64 463.746v-384h21.334v384h-21.334zM192 463.746v-384h21.334v384h-21.334z" fill="currentColor"></path></svg>
                            &nbsp; &nbsp; Your Library
                    </li >
                    </Link>
                    <p></p>
                    <a href='https://kinespotify.herokuapp.com/' >
                        <li>
                            <svg width="24" height="24" viewBox="0 0 80 90" xmlns="http://www.w3.org/2000/svg"><title>User Icon</title><path d="M67.74 61.78l-14.5-8.334c-.735-.422-1.24-1.145-1.385-1.98-.145-.835.088-1.685.638-2.33l5.912-6.93c3.747-4.378 5.81-9.967 5.81-15.737v-2.256c0-6.668-2.792-13.108-7.658-17.67C51.622 1.92 45.17-.386 38.392.054c-12.677.82-22.607 11.772-22.607 24.934v1.483c0 5.77 2.063 11.36 5.81 15.736l5.912 6.933c.55.644.783 1.493.638 2.33-.143.834-.648 1.556-1.383 1.98l-14.494 8.33C4.7 66.077 0 74.15 0 82.844v6.76h3.333v-6.76c0-7.5 4.055-14.46 10.59-18.174l14.5-8.334c1.597-.918 2.692-2.487 3.007-4.302.315-1.815-.19-3.66-1.387-5.06l-5.913-6.936c-3.23-3.775-5.01-8.594-5.01-13.57v-1.484c0-11.41 8.562-20.9 19.488-21.608 5.85-.377 11.415 1.61 15.67 5.598 4.26 3.992 6.605 9.404 6.605 15.24v2.254c0 4.976-1.778 9.796-5.01 13.57l-5.915 6.935c-1.195 1.4-1.7 3.246-1.386 5.06.313 1.816 1.41 3.385 3.008 4.303l14.507 8.338c6.525 3.71 10.58 10.67 10.58 18.17v6.76H80v-6.76c0-8.695-4.7-16.768-12.26-21.063z" fill="currentColor" fillRule="evenodd"></path></svg>
                            &nbsp; &nbsp; Get access
                    </li>
                    </a>
                </ul>

                {/* RECENTLY List */}
                <h6 className='recently'>RECENTLY PLAYED</h6>
                <ul className="list-unstyled">
                    {
                        //List recently by map
                        this.state.recently.map((item, index) => {
                            //Condition for getting active class
                            const className = this.state.choosing === index ? 'active-media' : '';
                            return (
                                <li key={index}
                                    onClick={() => this.changeRecently(index, item)}
                                    className={'media ' + className}>
                                    <Link to={"/album/" + item.id}>
                                        <div className="media-body">
                                            <h5 className="mt-0 mb-1">{item.album}</h5>
                                            {item.type}
                                        </div>
                                    </Link>
                                </li>
                            )
                        })
                    }
                </ul>
                <div className='bottom-thing'>
                    <h6><FontAwesomeIcon className='icon' icon={faArrowAltCircleDown} /> Install App</h6>
                    <div className='profile'>
                        <img className='avatar' alt='avatar' src={this.state.avatar} />
                        <div className='username'>{this.state.user}</div>
                    </div>

                </div>

            </div>
        )
    }

    //Get avatar and user's name
    componentDidMount = () => {
        const url = `https://raw.githubusercontent.com/hailduong/lyduong/master/react-assignment/spotify/apis/userInfo.json`;
        fetch(url)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    user: data.name,
                    avatar: data.avatar,
                });
            })
            .catch(e => console.log('error', e));
    }
}

export default LeftSideBar
