import React, { Component } from 'react';
import './Search.css';
import { BrowserRouter as Router, Route, Link, Switch } from "react-router-dom";
import searched from '../../img/seaching.gif';

//Component
import ListAlbums from './ListAlbums';
import ListTracks from './ListTracks';
import TopResult from './TopResult';

export class Search extends Component {
    constructor(props) {
        super(props);
        this.state = {
            album: '',
            tracks: '',
            firstAlbum: '',
            position: 1,
            isSearch: false,
            isFound: false,
        };
        //Set active class for menu after reload match url 
        if(window.location.pathname.startsWith("/search/songs")){
            this.state.position = 2;
        }
        else if (window.location.pathname.startsWith("/search/albums")){
            this.state.position = 3;
        }
        else {
            this.state.position = 1;
        }
    }

    //Convert track's duration to minute
    converTime(millis) {
        let minutes = Math.floor(millis / 60000);
        let seconds = ((millis % 60000) / 1000).toFixed(0);
        return minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    }

    //Change tab's position 
    changePosotion(index) {
        this.setState({
            position: index
        });
    }

    //Search function
    searchSpotify = (e) => {
        //Execute function when hit Enter button
        if (e.key === "Enter") {
            //Reset state when start to search and set state to "Seaching"
            this.setState({
                album: '',
                tracks: '',
                firstAlbum: '',
                isSearch: true
            },
                //After that call search API
                () => {
                    //get access token
                    const token = this.props.accessToken;
                    console.log(token);
                    const url = `https://api.spotify.com/v1/search?query=${this.searchInput.value}&limit=20&type=album,track`;
                    fetch(url, {
                        //config header
                        headers: {
                            "Content-Type": "application/json",
                            "Accept": "application/json",
                            "Authorization": `Bearer ${token}`,
                        }
                    })
                        .then(response => response.json())
                        .then(data => {
                            console.log(data.albums.items);
                            //when items are not found
                            if (data.albums.items.length < 1 || data.tracks.items.length < 1) {
                                this.setState({
                                    isFound: false,
                                    firstAlbum: "Haha th ngu"
                                }, () => console.log(this.state))
                            }
                            else {
                                //Convert track's duration to minute
                                let dataChangeType = data.tracks.items;
                                dataChangeType.map((item) => {
                                    item.duration_ms = this.converTime(item.duration_ms);
                                });
                                //When found any items
                                this.setState({
                                    album: data.albums.items,
                                    tracks: data.tracks.items,
                                    firstAlbum: data.albums.items[0],
                                    isFound: true,
                                    isSearch: false
                                }, () => console.log(this.state))
                            }
                        })
                        .catch(e => {
                            //When access token expire. redirect to login page to get a new one
                            window.location.replace("https://kinespotify.herokuapp.com/");
                            console.log('Haha th ngu! Code  li');
                            console.log('error', e)
                        });
                })

        }

    }

    render() {
        // View while Search function is executing
        const searching = (
            <div className='searching'>
                <div className='img-load'>
                    <img className='img-size' alt='seaching' src={searched}/>
                    <h2>Searching...</h2>
                </div>
            </div>
        );
        
        //View before Search function executed
        const beforeSearch = (
            <div className='intro'>
                <h1> Search Spotify</h1>
                <h6>Find your favorite songs, artists, albums, podcasts and playlists</h6>
            </div>
        );
        // View when API not found any items
        const notFound = (
            <div className='notFound'>
                <h1>Not found</h1>
            </div>
        );

        //View when API return any items
        const itemFound = (
            <div>
                <div className='search-container'>
                    <ul className='menu-search'>
                        <Link className={this.state.position === 1 ? 'active' : ''} onClick={() => this.changePosotion(1)} to="/search">
                            <li>Top Results</li>
                        </Link>
                        <Link className={this.state.position === 2 ? 'active' : ''} onClick={() => this.changePosotion(2)} to="/search/songs">
                            <li>Songs</li>
                        </Link>
                        <Link className={this.state.position === 3 ? 'active' : ''} onClick={() => this.changePosotion(3)} to="/search/albums">
                            <li>Albums</li >
                        </Link>
                    </ul>
                </div>
                <div className='search-content'>
                    <Switch>
                        {//App will check if has data, then redirect to component

                            //Searched albums component
                            this.state.album !== '' && this.state.tracks !== '' ? <Route path="/search/albums" render={() => <ListAlbums items={this.state.album} />} /> : <div></div>
                        }
                        {
                            //Searched tracks component
                            this.state.tracks !== '' && <Route path="/search/songs" render={() => <ListTracks items={this.state.tracks} />} />
                        }
                        {
                            //Top result component
                            this.state.album !== '' && <Route path="/search" render={() => <TopResult firstAlbum={this.state.firstAlbum} tracks={this.state.tracks} />} />
                        }
                    </Switch>
                </div>
            </div>
        );
        return (
            <div className='search'>

                {/* Search bar */}
                <div className="input-group">
                    <input type="text" placeholder="Start typing ... (hit Enter to search)"
                        ref={(input) => { this.searchInput = input; }}
                        spellCheck="false"
                        onKeyPress={this.searchSpotify}
                    />
                </div>

                {
                    //Change view when API respone or not
                    this.state.firstAlbum === '' ?
                    <div className='containerSearching'>
                        {// change view when status is seaching or not
                            this.state.isSearch === true ? searching : beforeSearch
                        }
                    </div>
                    :
                    <div>
                        {// change view when status is found or not found
                            this.state.isFound === true ? itemFound : notFound
                        }
                    </div>
                }
            </div>
        )
    }

    //Focus to search field after complete render
    componentDidMount() {
        this.searchInput.focus();
    }
}

export default Search
