import React, { Component } from 'react';
import './Album.css';
import logo from '../../img/spoHome.gif'

export class Home extends Component {
  render() {
    return (
      <div className='home'>
        <h1>Welcome to Spotify</h1>
        <img alt='logo' src={logo}/>
      </div>
    )
  }
}

export default Home
