import React, { Component } from 'react';
import './Middle.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisH } from '@fortawesome/free-solid-svg-icons';
import loading from '../../img/loading.gif'

export class Middle extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      artis: '',
      cover: '',
      year: '',
      track: [],
      show: false,
      isLoad: false
    };
  }


  componentWillReceiveProps(nextProps) {
    console.log(nextProps);
    //check if data is exist
    if (nextProps.choosenTrack !== '') {
      //set state when data has received
      this.setState({
        name: nextProps.choosenTrack.album.album.album,
        artis: nextProps.choosenTrack.album.tracks.artist,
        cover: nextProps.choosenTrack.album.album.cover,
        year: nextProps.choosenTrack.album.tracks.year,
        track: nextProps.choosenTrack.album.tracks.tracks.length,
        playing: nextProps.choosenTrack.album.playing
      });
    }
  }

  // Change state and send data when clicking on Play/Stop button
  toggleShow = () => {
    // When Play button clicked
    if (this.state.show === true) {
      //Set it to Stop state
      this.setState({
        show: false
      });
      //Data for sending to parent component
      const stateSend = {
        album: this.props.choosenTrack.album.album,
        playing: 0,
        tracks: this.props.choosenTrack.album.tracks
      }
      //Send data through props
      this.props.update(stateSend);
    }

    // When Stop button clicked
    else if (this.state.show === false) {
      //Set it to Play state
      this.setState({
        show: true
      });
      //Data for sending to parent component
      const stateSend = {
        album: this.props.choosenTrack.album.album,
        playing: 0,
        tracks: this.props.choosenTrack.album.tracks
      }
      //Send data through props
      this.props.update(stateSend);
    }
  }

  render() {

    //Loading view will display when data is getting
    if (this.state.isLoad === false) {
      return (
        <div className='loading'>
          <div className='img-load'>
            <img className='imgLoad' src={loading} alt='loading' />
            <h2>Loading...</h2>
          </div>
        </div>
      )

      //View after data is gotten
    }
    else {
      return (
        <div className='middle'>
          <img className='cover' alt='cover' src={this.state.cover} />
          <h2>{this.state.name}</h2>
          <h6 className='actor'>{this.state.artis}</h6>
          {/* Toggle PLay/Stop button */}
          {this.state.show ? <button onClick={this.toggleShow} type="button" className="btn btn-success">PLAY</button> : <button onClick={this.toggleShow} type="button" className="btn btn-danger">STOP</button>}
          <br />
          <h6 className='actor'>{this.state.year} . {this.state.track} SONGS</h6>
          <div className='remove'>
            <h6>REMOVE FROM YOUR LIBRARY</h6>
            <FontAwesomeIcon className='icon' icon={faEllipsisH} color="#ddd" />
          </div>
        </div>
      )
    }
  }


  // Re-get data by album ID, use this function for reload at the same Url, we still have link's ID
  componentDidMount = () => {
    const url = `https://hailduong.github.io/lyduong/react-assignment/spotify-2/apis/album/${this.props.id}.json`;
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          name: data.name,
          artis: data.artist,
          cover: data.covers.large,
          year: data.year,
          track: data.tracks.length,
          isLoad: true
        });
      }, () => console.log(this.state))
      .catch(e => console.log('error', e));
  }
}

export default Middle
